// src/components/Partners.js

import React from 'react';
import { Box, Typography, Grid, Button } from '@mui/material';
import SquareCloudLogo from '../images/SquareCloud.png';
import JohorPortLogo from '../images/JohorPort.png';
import MalaysiaAirportsLogo from '../images/MalaysiaAirports.png';
import GentingBerhadLogo from '../images/GentingBerhad.png';
import GovernmentLogo from '../images/Government.png';

const Partners = () => {
  const allPartners = [
    { name: 'SquareCloud', logo: SquareCloudLogo },
    { name: 'JohorPort', logo: JohorPortLogo },
    { name: 'MalaysiaAirports', logo: MalaysiaAirportsLogo },
    { name: 'Genting Berhad', logo: GentingBerhadLogo },
    { name: 'Government', logo: GovernmentLogo },
  ];

  return (
    <Box id="partners" sx={{ mb: 8, px: { xs: 2, md: 4 }, py: 4 }}>
      {/* Section Title */}
      <Typography variant="h4" align="center" gutterBottom>
        Our Trusted Partners
      </Typography>

      {/* Section Description */}
      <Typography
        variant="h6"
        align="center"
        sx={{ mb: 4, color: 'white', fontWeight: 400 }}
      >
        We collaborate with leading organizations to deliver innovative solutions, ensuring quality, reliability, and excellence in every project we undertake.
      </Typography>

      {/* Frosted Container for Partner Logos */}
      <Box
        sx={{
          backgroundColor: 'rgba(204, 222, 255, 0.9)', // Frosted glass effect
          borderRadius: 2,
          padding: 4,
          boxShadow: 3,
        }}
      >
        <Grid container spacing={4} justifyContent="center">
          {allPartners.map((partner, index) => (
            <Grid item xs={6} sm={3} md={2} key={index}>
              <Box
                sx={{
                  padding: 2,
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center',
                  height: '100px',
                  transition: 'transform 0.3s',
                  '&:hover': {
                    transform: 'scale(1.05)', // Slightly enlarge on hover
                  },
                }}
              >
                <img
                  src={partner.logo}
                  alt={partner.name}
                  style={{ maxHeight: '80px', objectFit: 'contain' }}
                  loading="lazy"
                />
              </Box>
            </Grid>
          ))}
        </Grid>
      </Box>
              {/* Learn More Button */}
              <Box textAlign="center" mt={4}>
          <Button
            variant="contained"
            sx={{
              backgroundColor: 'secondary.main',
              color: 'primary.main',
              '&:hover': {
                backgroundColor: 'grey.300',
              },
              fontWeight: 'bold',
              textTransform: 'none',
              alignSelf: 'center',
            }}
          >
            Learn More
          </Button>
        </Box>
    </Box>
  );
};

export default Partners;
