// src/components/Footer.js

import React from 'react';
import { Box, Typography, Grid, Link, Button } from '@mui/material';
import HomeIcon from '@mui/icons-material/Home';
import InfoIcon from '@mui/icons-material/Info';
import GroupIcon from '@mui/icons-material/Group';
import BuildIcon from '@mui/icons-material/Build';
import ContactMailIcon from '@mui/icons-material/ContactMail';
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import FacebookIcon from '@mui/icons-material/Facebook';
import TwitterIcon from '@mui/icons-material/Twitter';

const Footer = () => {
  return (
    <Box
      component="footer"
      sx={{
        backgroundColor: 'primary.main',
        color: 'secondary.main',
        px: { xs: 2, md: 6 },
        py: { xs: 4, md: 6 },
      }}
    >
      <Grid container spacing={4} justifyContent={'center'}>
        {/* Company Info */}
        <Grid item xs={12} sm={6} md={3}>
          <Typography variant="h6" gutterBottom>
            DTSYS
          </Typography>
          <Typography variant="body2" sx={{ lineHeight: 1.6 }}>
            03-6211 3678<br />
            <Link href="mailto:info@dtsys.tech" sx={{ color: 'secondary.main', textDecoration: 'none' }}>
              info@dtsys.tech
            </Link><br />
            No. B-5-13, Gateway Corporate Suites,<br />
            Mont Kiara, 50480 Kuala Lumpur,<br />
            Wilayah Persekutuan Malaysia
          </Typography>
        </Grid>

        {/* Quick Links */}
        <Grid item xs={12} sm={6} md={3}>
          <Typography variant="h6" gutterBottom>
            Quick Links
          </Typography>
          <Link href="/dtsys" sx={{ color: 'secondary.main', textDecoration: 'none', display: 'flex', alignItems: 'center', mb: 1 }}>
            <HomeIcon sx={{ mr: 1 }} /> Home
          </Link>
          <Link href="/aboutus" sx={{ color: 'secondary.main', textDecoration: 'none', display: 'flex', alignItems: 'center', mb: 1 }}>
            <InfoIcon sx={{ mr: 1 }} /> About Us
          </Link>
          <Link href="/partners" sx={{ color: 'secondary.main', textDecoration: 'none', display: 'flex', alignItems: 'center', mb: 1 }}>
            <GroupIcon sx={{ mr: 1 }} /> Partners
          </Link>
          <Link href="/dtsys" sx={{ color: 'secondary.main', textDecoration: 'none', display: 'flex', alignItems: 'center', mb: 1 }}>
            <BuildIcon sx={{ mr: 1 }} /> Solutions
          </Link>
          <Link href="/contact" sx={{ color: 'secondary.main', textDecoration: 'none', display: 'flex', alignItems: 'center', mb: 1 }}>
            <ContactMailIcon sx={{ mr: 1 }} /> Contact Us
          </Link>
        </Grid>

        {/* Social Media Links */}
        <Grid item xs={12} sm={6} md={3}>
          <Typography variant="h6" gutterBottom>
            Follow Us On:
          </Typography>
          <Box sx={{ display: 'flex', gap: 2, mt: 1 }}>
            <Link href="https://www.linkedin.com" aria-label="LinkedIn" sx={{ color: 'secondary.main' }}>
              <LinkedInIcon fontSize="large" />
            </Link>
            <Link href="https://www.facebook.com" aria-label="Facebook" sx={{ color: 'secondary.main' }}>
              <FacebookIcon fontSize="large" />
            </Link>
            <Link href="https://www.twitter.com" aria-label="Twitter" sx={{ color: 'secondary.main' }}>
              <TwitterIcon fontSize="large" />
            </Link>
          </Box>
        </Grid>
      </Grid>

      {/* Footer Bottom */}
      <Box textAlign="center" justifyContent="center" mt={4}>
        <Typography variant="body2">
          ©2024 by DTSYS. All rights reserved.
        </Typography>
      </Box>
    </Box>
  );
};

export default Footer;
