// src/pages/ITSolutions.js

import React from 'react';
import { Box, Typography, Button, Grid, Card, CardContent } from '@mui/material';
import BodyImage from '../images/Body.jpg';
import ContactUs from './ContactUs';

const ITSolutions = () => {
  const sections = [
    {
      title: 'IT Consulting for Software, Infrastructure and Cyber Security',
      description:
        'We provide IT consulting services for software, infrastructure, and cyber security solutions for airports, ports, and electricity networks. Our team of experts can help you with your IT needs, from software development to infrastructure planning and cyber security solutions.  ',
    },
    {
      title: 'Data Analytics and Business Intelligence',
      description:
        'We offer data analytics and business intelligence services to help you make informed decisions. Our experts can help you analyze your data and provide actionable insights to drive your business forward.  ',
    },
    {
      title: 'IT Application Development and Maintenance',
      description:
        'We provide IT application development and maintenance services to help you keep your systems up to date and running smoothly. Our team of experts can help you with everything from application design to maintenance and support.  ',
    },
    {
      title: 'Government Tender Participation',
      description:
        'We are able to participate in government tenders for IT consulting services, software development, and cyber security solutions.  ',
    },
  ];

  return (
    <Box
      sx={{
        backgroundImage: `url(${BodyImage})`,
        backgroundAttachment: 'fixed',
        backgroundSize: 'cover',
        backgroundPosition: 'center',
        px: { xs: 2, md: 4 },
        py: 6,
        color: '#fff',
      }}
    >
      <Typography variant="h3" align="center" gutterBottom sx={{ fontWeight: 'bold' }}>
        IT Solutions
      </Typography>
      <Typography variant="h6" align="center" paragraph sx={{ opacity: 0.9 }}>
        Explore our comprehensive IT consulting and service offerings tailored to your business needs.
      </Typography>

      <Grid container spacing={4}>
        {sections.map((section, index) => (
          <Grid item xs={12} key={index}>
            <Card
              sx={{
                backgroundColor: 'rgba(0, 0, 0, 0.6)',
                borderRadius: 2,
                boxShadow: 3,
                padding: 4,
                transition: 'transform 0.3s',
                '&:hover': {
                  transform: 'translateY(-5px)',
                },
              }}
            >
              <CardContent>
                <Typography variant="h4" gutterBottom sx={{ fontWeight: 'bold', color: 'secondary.main' }}>
                  {section.title}
                </Typography>
                <Typography variant="body1" sx={{ color: 'rgba(255, 255, 255, 0.85)', mb: 2 }}>
                  {section.description}
                </Typography>
              </CardContent>
            </Card>
          </Grid>
        ))}
      </Grid>
      <Grid sx={{mt: '100px'}}>
      <ContactUs />
      </Grid>
    </Box>
  );
};

export default ITSolutions;
