// src/pages/PartnersPage.js

import React from 'react';
import { Box, Typography, Grid, Button } from '@mui/material';
import SquareCloudLogo from '../images/SquareCloud.png';
// Import other partner logos as needed
import JohorPortLogo from '../images/JohorPort.png';
import MalaysiaAirportsLogo from '../images/MalaysiaAirports.png';
import GentingBerhadLogo from '../images/GentingBerhad.png';
import GovernmentLogo from '../images/Government.png';
import BodyImage from '../images/Body.jpg';

const PartnersPage = () => {
  const allPartners = [
    { name: 'JohorPort', logo: JohorPortLogo },
    { name: 'MalaysiaAirports', logo: MalaysiaAirportsLogo },
    { name: 'Genting Berhad', logo: GentingBerhadLogo },
    { name: 'Government', logo: GovernmentLogo },
    // Add more partners as needed
  ];

  return (
    <Box
      sx={{
        backgroundImage: `url(${BodyImage})`,
        backgroundAttachment: 'fixed',
        backgroundSize: 'cover',
        backgroundPosition: 'center',
        px: { xs: 2, md: 4 },
        py: 6,
        color: '#fff',
      }}
    >
      {/* Introduction to Partnerships */}
      <Typography variant="h4" align="center" gutterBottom sx={{ fontWeight: 'bold' }}>
        Our Strategic Partnerships
      </Typography>
      <Typography variant="h6" align="center" paragraph sx={{ mb: 4, color: 'white' }}>
        At DTSYS, we believe in the power of collaboration. Our strategic partnerships enable us to deliver cutting-edge solutions and enhance our service offerings, ensuring our clients receive the best in technology and support.
      </Typography>

      {/* Featured Partnership: SquareCloud */}
      <Box
        sx={{
          backgroundColor: 'rgba(0, 0, 0, 0.54)',
          backdropFilter: 'blur(10px)',
          borderRadius: 2,
          p: 4,
          mb: 6,
          textAlign: 'center',
        }}
      >
        <Typography variant="h5" gutterBottom sx={{ fontWeight: 'bold' }}>
          Revolutionizing Chiller Optimization with SquareCloud's MaximizeEE Technology
        </Typography>
        <Typography variant="body1" sx={{ mb: 2 }}>
          We’re excited to announce our strategic partnership with SquareCloud, a leader in cloud technology and infrastructure solutions. Together, we are delivering cutting-edge, scalable cloud services that optimize performance, reduce costs, and enhance digital experiences for our clients.
        </Typography>
        <Typography variant="body2" sx={{ mb: 4, fontStyle: 'italic' }}>
          MaximizeEE, short for "Maximize Energy Efficiency," focuses on optimizing energy consumption in chiller systems within large facilities. It uses advanced AI/ML algorithms to analyze real-time data from IoT sensors, identifying inefficiencies and predicting performance. By dynamically adjusting operational parameters, the system ensures optimal chiller performance under varying loads, reducing energy consumption and lowering carbon emissions.
        </Typography>
        <Button
          variant="contained"
          sx={{
            backgroundColor: 'secondary.main',
            color: 'primary.main',
            '&:hover': { backgroundColor: 'grey.300' },
            fontWeight: 'bold',
            textTransform: 'none',
          }}
        >
          Learn More About MaximizeEE
        </Button>
      </Box>

      {/* Other Partners Section */}
      <Typography variant="h5" align="center" gutterBottom sx={{ mb: 4 }}>
        Our Valued Partners
      </Typography>
      {/* Frosted Container for Partner Logos */}
      <Box
        sx={{
          backgroundColor: 'rgba(204, 222, 255, 0.9)', // Frosted glass effect
          borderRadius: 2,
          padding: 4,
          boxShadow: 3,
        }}
      >
        <Grid container spacing={4} justifyContent="center">
          {allPartners.map((partner, index) => (
            <Grid item xs={6} sm={3} md={2} key={index}>
              <Box
                sx={{
                  padding: 2,
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center',
                  height: '100px',
                  transition: 'transform 0.3s',
                  '&:hover': {
                    transform: 'scale(1.05)', // Slightly enlarge on hover
                  },
                }}
              >
                <img
                  src={partner.logo}
                  alt={partner.name}
                  style={{ maxHeight: '80px', objectFit: 'contain' }}
                  loading="lazy"
                />
              </Box>
            </Grid>
          ))}
        </Grid>
      </Box>
    </Box>
  );
};

export default PartnersPage;
