// src/components/KeyServices.js

import React from 'react';
import { Box, Typography, Grid, Card, Button, Fade } from '@mui/material';
import PeopleIcon from '@mui/icons-material/People';
import EngineeringIcon from '@mui/icons-material/Engineering';
import SecurityIcon from '@mui/icons-material/Security';
import CodeIcon from '@mui/icons-material/Code';

const KeyServices = () => {
  // Scroll helper function (if needed)
  const scrollToSection = (id) => {
    const element = document.getElementById(id);
    if(element) {
      element.scrollIntoView({ behavior: 'smooth' });
    }
  };

  // Array of services with updated content and relevant icons
  const services = [
    {
      icon: <PeopleIcon sx={{ fontSize: '3rem', mb: 1 }} />,
      title: 'Talent',
      description: 'Providing the expertise needed to address your technology challenges through a highly skilled workforce.',
      link: '#',
    },
    {
      icon: <EngineeringIcon sx={{ fontSize: '3rem', mb: 1 }} />,
      title: 'Critical Infrastructure',
      description: 'Building strong foundations for economic growth, enhancing services like transportation, energy, and communication.',
      link: '#',
    },
    {
      icon: <SecurityIcon sx={{ fontSize: '3rem', mb: 1 }} />,
      title: 'Cyber Security',
      description: 'Safeguarding operations with robust measures to prevent data breaches and financial losses.',
      link: '#',
    },
    {
      icon: <CodeIcon sx={{ fontSize: '3rem', mb: 1 }} />,
      title: 'Software',
      description: 'Delivering secure, enterprise-grade solutions trusted by businesses worldwide.',
      link: '#',
    },
  ];

  return (
    <Box
      id="services"
      sx={{
        marginBottom: 8,
        px: { xs: 2, md: 4 },
        py: 4,
      }}
    >
      {/* Section Title */}
      <Typography variant="h4" sx={{fontWeight: 'bold'}} align="center" gutterBottom>
        Empowering Business With Technology
      </Typography>

      {/* Intro Paragraph */}
      <Typography variant="h6" align="center" paragraph>
        At DTSYS, we empower businesses through innovative solutions tailored to your unique needs. From cloud analytics modernization to enhancing application versatility and chiller optimization, our experts deliver strategies that drive growth and unlock your full potential in the digital landscape.
      </Typography>

      {/* Services Grid */}
      <Grid container spacing={4} justifyContent="center">
        {services.map((service, index) => (
          <Grid item xs={12} sm={6} md={3} key={index}>
            <Fade in={true} timeout={1000}>
              <Card
                sx={{
                  backgroundColor: '#0b1436',
                  color: 'text.primary',
                  borderRadius: 2,
                  padding: 3,
                  textAlign: 'center',
                  boxShadow: 3,
                  height: '100%',
                  display: 'flex',
                  flexDirection: 'column',
                  justifyContent: 'space-between',
                  transition: 'transform 0.3s',
                  '&:hover': {
                    transform: 'scale(1.05)',
                  },
                }}
              >
                <Box>
                  {/* Service Icon */}
                  {service.icon}

                  {/* Service Title */}
                  <Typography variant="h6" sx={{ fontWeight: 'bold', mb: 1 }}>
                    {service.title}
                  </Typography>

                  {/* Service Description */}
                  <Typography variant="body1" sx={{ mb: 2 }}>
                    {service.description}
                  </Typography>
                </Box>

              </Card>
            </Fade>
          </Grid>
        ))}
      </Grid>
    </Box>
  );
};

export default KeyServices;
