// src/pages/AboutUs.js

import React from 'react';
import {
  Box,
  Typography,
  Container,
  Grid,
  Paper,
  Stack,
} from '@mui/material';
import Lottie from 'lottie-react';

// Import your Lottie animation JSON files
import MissionAnimation from '../images/Mission.json';
import StoryAnimation from '../images/Story.json';
import LeadershipAnimation from '../images/Leadership.json';

import BodyImage from '../images/Body.jpg';
import TitleImage from '../images/Title.jpg';

const AboutUs = () => {
  const sections = [
    {
      title: 'Our Mission',
      content:
        "At DTSYS, we are committed to providing innovative solutions in software development, infrastructure design, and cyber security. Our goal is to help businesses in Malaysia's airports, ports, and electricity networks to operate more efficiently and securely. We take pride in being able to participate in government tenders and deliver exceptional results.",
      animation: MissionAnimation,
    },
    {
      title: 'Our Story',
      content:
        "DTSYS was founded in Malaysia with a mission to provide top-notch IT consulting services to businesses in the transportation and energy sectors. Our team of experts has years of experience in software development, infrastructure design, and cyber security. We strive to understand our clients' unique needs and deliver customized solutions that exceed their expectations.",
      animation: StoryAnimation,
    },
    {
      title: 'Experienced Leadership',
      content:
        "At DTSYS, we have a team of experienced leaders who are passionate about technology and committed to delivering exceptional results. Our team includes software developers, infrastructure designers, and cyber security experts who work together to provide innovative solutions to our clients. We take pride in our ability to understand our clients' needs and deliver solutions that meet and exceed their expectations.",
      animation: LeadershipAnimation,
    },
  ];

  return (
    <Box
      sx={{
        backgroundImage: `url(${BodyImage})`,
        backgroundAttachment: 'fixed',
        backgroundSize: 'cover',
        backgroundPosition: 'center',
        color: '#fff',
        minHeight: '100vh',
        display: 'flex',
        flexDirection: 'column',
      }}
    >
      {/* Top Section with Title */}
      <Box
        sx={{
          // backgroundImage: `url(${TitleImage})`,
          backgroundSize: 'cover',
          backgroundPosition: 'center',
          minHeight: '150px',
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          width: '100%',
          flexShrink: 0,
        }}
      >
        <Typography variant="h2" sx={{ color: '#fff', fontWeight: 'bold' }}>
          About Us
        </Typography>
      </Box>

      {/* Single Overlay for All Sections */}
      <Box
        sx={{
          backgroundColor: 'rgba(0, 0, 0, 0.54)',
          backdropFilter: 'blur(10px)',
          borderRadius: 2,
          p: { xs: 2, md: 4 },
          mt: { xs: 2, md: 4 },
          mb: { xs: 2, md: 4 },
          flexGrow: 1,
        }}
      >
        <Container maxWidth="lg">
          <Grid container spacing={4}>
            {sections.map((section, index) => (
              <Grid item xs={12} md={4} key={index}>
                <Paper
                  elevation={4}
                  sx={{
                    backgroundColor: 'rgba(255, 255, 255, 0.2)',
                    color: '#FFFFF',
                    height: '100%',
                    p: 3,
                    display: 'flex',
                    flexDirection: 'column',
                    alignItems: 'center',
                    textAlign: 'center',
                    transition: 'transform 0.3s, box-shadow 0.3s',
                    '&:hover': {
                      transform: 'scale(1.02)',
                      boxShadow: 6,
                    },
                  }}
                >
                  <Stack direction="column" alignItems="center" spacing={2}>
                    {/* Animated Icon */}
                    <Box sx={{ width: 80, height: 80 }}>
                      <Lottie
                        animationData={section.animation}
                        loop={true}
                        style={{ width: '100%', height: '100%' }}
                      />
                    </Box>
                    {/* Section Title */}
                    <Typography variant="h5" sx={{ fontWeight: 'bold' }}>
                      {section.title}
                    </Typography>
                  </Stack>
                  <Typography variant="body1" sx={{ mt: 2 }}>
                    {section.content}
                  </Typography>
                </Paper>
              </Grid>
            ))}
          </Grid>
        </Container>
      </Box>
    </Box>
  );
};

export default AboutUs;
