// src/components/Solutions.js

import React from 'react';
import { Grid, Typography, Card, Button, Box } from '@mui/material';
import AcUnitIcon from '@mui/icons-material/AcUnit'; // Example icon
import ComputerIcon from '@mui/icons-material/Computer';

const Solutions = () => {
  const solutions = [
    {
      icon: <AcUnitIcon sx={{ fontSize: '3rem', mb: 1 }} />,
      title: 'Chiller Optimization Software',
      description: 'Optimize energy usage with advanced chiller management solutions.',
      link: '/chiller', // Updated to the Chiller page route
    },
    {
      icon: <ComputerIcon sx={{ fontSize: '3rem', mb: 1 }} />,
      title: 'Customized IT Solutions',
      description: 'Tailored IT services to meet your unique business requirements.',
      link: '/itsolutions',
    },
    // Add more solutions as needed
  ];

  return (
    <Box
      id="solutions"
      sx={{
        marginBottom: 8,
        px: { xs: 2, md: 4 },
        py: 4,
      }}
    >
      <Typography variant="h4" align="center" gutterBottom>
        Our Solutions
      </Typography>
      <Typography variant="h6" align="center" paragraph>
        Explore our comprehensive range of solutions designed to meet your business needs.
      </Typography>
      <Grid container spacing={4} justifyContent="center">
        {solutions.map((solution, index) => (
          <Grid item xs={12} sm={6} md={4} key={index}>
            <Card
              sx={{
                backgroundColor: '#0b1436',
                color: 'text.primary',
                borderRadius: 2,
                padding: 2,
                textAlign: 'center',
                height: '100%',
                boxShadow: 3,
                display: 'flex',
                flexDirection: 'column',
                justifyContent: 'space-between',
              }}
            >
              <Box>
                {solution.icon}
                <Typography variant="h6" sx={{ fontWeight: 'bold', mb: 1 }}>
                  {solution.title}
                </Typography>
                <Typography variant="body1" sx={{ mb: 2 }}>
                  {solution.description}
                </Typography>
              </Box>
              <Button
                variant="contained"
                href={solution.link} // Uses the route link
                sx={{
                  backgroundColor: 'secondary.main',
                  color: 'primary.main',
                  '&:hover': {
                    backgroundColor: 'grey.300',
                  },
                  fontWeight: 'bold',
                  textTransform: 'none',
                  alignSelf: 'center',
                }}
              >
                Learn More
              </Button>
            </Card>
          </Grid>
        ))}
      </Grid>
    </Box>
  );
};

export default Solutions;
