// src/components/Header.js

import React from 'react';
import { AppBar, Toolbar, Button, Box, Menu, MenuItem } from '@mui/material';
import { useNavigate } from 'react-router-dom';
import logo from '../images/Logo.png';

const Header = () => {
  const navigate = useNavigate();

  // State for Solutions Menu
  const [anchorEl, setAnchorEl] = React.useState(null);
  const open = Boolean(anchorEl);

  const handleSolutionsClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleSolutionsClose = () => {
    setAnchorEl(null);
  };

  return (
    <AppBar
      position="sticky"
      sx={{
        backgroundColor: 'primary.main',
      }}
    >
      <Toolbar>
        {/* Logo Section */}
        <Box display="flex" alignItems="center" sx={{ cursor: 'pointer' }} onClick={() => navigate('/dtsys')}>
          <img
            src={logo}
            alt="DTSYS Logo"
            style={{
              height: '40px',
            }}
          />
        </Box>

        {/* Navigation Links */}
        <Box
          sx={{
            marginLeft: 'auto',
            display: 'flex',
            gap: 3,
          }}
        >
          <Button
            onClick={() => navigate('/dtsys')}
            sx={{
              color: 'secondary.main',
              textTransform: 'none',
              fontWeight: 'bold',
              fontSize: '1rem',
              '&:hover': {
                backgroundColor: '#979eba',
              },
            }}
          >
            Home
          </Button>

          <Button
            onClick={() => navigate('/aboutus')}
            sx={{
              color: 'secondary.main',
              textTransform: 'none',
              fontWeight: 'bold',
              fontSize: '1rem',
              '&:hover': {
                backgroundColor: '#979eba',
              },
            }}
          >
            About Us
          </Button>

          <Button
            onClick={() => navigate('/partners')}
            sx={{
              color: 'secondary.main',
              textTransform: 'none',
              fontWeight: 'bold',
              fontSize: '1rem',
              '&:hover': {
                backgroundColor: '#979eba',
              },
            }}
          >
            Partners
          </Button>

          {/* Solutions Dropdown */}
          <Button
            onClick={handleSolutionsClick}
            sx={{
              color: 'secondary.main',
              textTransform: 'none',
              fontWeight: 'bold',
              fontSize: '1rem',
              '&:hover': {
                backgroundColor: '#979eba',
              },
            }}
          >
            Solutions
          </Button>
          <Menu
            anchorEl={anchorEl}
            open={open}
            onClose={handleSolutionsClose}
            disableScrollLock  // <-- Added prop here
            anchorOrigin={{
              vertical: 'bottom',
              horizontal: 'right',
            }}
            transformOrigin={{
              vertical: 'top',
              horizontal: 'right',
            }}
            PaperProps={{
              sx: {
                backgroundColor: '#0b1436',
              },
            }}
          >
            <MenuItem
              onClick={() => {
                handleSolutionsClose();
                navigate('/chiller');
              }}
              sx={{
                color: '#FFFFFF',
              }}
            >
              Chiller Optimization
            </MenuItem>
            <MenuItem
              onClick={() => {
                handleSolutionsClose();
                navigate('/itsolutions');
              }}
              sx={{
                color: '#FFFFFF',
              }}
            >
              IT Solutions
            </MenuItem>
          </Menu>

          <Button
            onClick={() => navigate('/contactus')}
            sx={{
              color: 'secondary.main',
              textTransform: 'none',
              fontWeight: 'bold',
              fontSize: '1rem',
              '&:hover': {
                backgroundColor: '#979eba',
              },
            }}
          >
            Contact Us
          </Button>
        </Box>
      </Toolbar>
    </AppBar>
  );
};

export default Header;
