// src/App.js

import React from 'react';
import { ThemeProvider } from '@mui/material/styles';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import CssBaseline from '@mui/material/CssBaseline';
import Header from './components/Header';
import HeroSection from './components/HeroSection';
import KeyServices from './components/KeyServices';
import Solutions from './components/Solutions';
import Partners from './components/Partners';
import ContactUs from './components/ContactUs';
import Footer from './components/Footer';
import BodyWrapper from './components/BodyWrapper'; // Import the BodyWrapper
import Chiller from './components/Chiller';
import ITSolutions from './components/ITSolutions';
import AboutUs from './components/AboutUs';
import PartnersPage from './components/PartnersPage';
import ContactUsPage from './components/ContactUsPage';
import theme from './theme/theme'; // Import the theme

function App() {
  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <Router>
        <Header />
        <Routes>
          <Route
            path="/dtsys"
            element={
              <>
                <HeroSection />
                <BodyWrapper>
        <KeyServices />
        <Solutions />
        <Partners />
        <ContactUs />
      </BodyWrapper>
              </>
            }
          />
          <Route path="/chiller" element={<Chiller />} /> 
          <Route path="/itsolutions" element={<ITSolutions />} /> 
          <Route path="/aboutus" element={<AboutUs />} /> 
          <Route path="/partners" element={<PartnersPage />} /> 
          <Route path="/contactus" element={<ContactUsPage />} /> 
        </Routes>
        <Footer />
      </Router>
    </ThemeProvider>
  );
}

export default App;
