// src/components/ContactUs.js

import React from 'react';
import { Box, Typography, Grid, TextField, Button, Link } from '@mui/material';

const ContactUs = () => {
  return (
    <Box
      id="contact"
      sx={{
        backgroundColor: 'rgba(138, 185, 255, 0.2)', // Frosted glass effect
        backdropFilter: 'blur(15px)', // Frosted glass blur
        borderRadius: 2,
        padding: 10,
        boxShadow: 3,
        marginBottom: 8,
        maxWidth: '1500px', // Constrain the width
        mx: 'auto', // Center horizontally
      }}
    >
      <Grid container spacing={4}>
        {/* Column 1: Title, Description, and Contact Details */}
        <Grid item xs={12} md={4}>
          <Typography variant="h4" gutterBottom sx={{fontWeight: 'bold'}}>
            Empower Your Business with DTSYS
          </Typography>
          <Typography variant="body1" paragraph>
            At DTSYS, we are committed to helping businesses leverage the power of technology to achieve their goals. Contact us today to learn how we can help you take your business to the next level.
          </Typography>
          <Typography variant="h6" gutterBottom sx={{fontWeight: 'bold'}}>
            Contact Details
          </Typography>
          <Typography variant="body1" sx={{ mb: 1 }}>
            Phone: <Link href="tel:0362113678" sx={{ color: 'secondary.main', textDecoration: 'none' }}>03-6211 3678</Link>
          </Typography>
          <Typography variant="body1" sx={{ mb: 1 }}>
            Email: <Link href="mailto:info@dtsys.tech" sx={{ color: 'secondary.main', textDecoration: 'none' }}>info@dtsys.tech</Link>
          </Typography>
          <Typography variant="body1" sx={{ mb: 2 }}>
            Address: <br /> No. B-5-13, Gateway Corporate Suites,<br />
            Mont Kiara, 50480 Kuala Lumpur,<br />
            Wilayah Persekutuan, Malaysia
          </Typography>
        </Grid>

        {/* Column 2: Contact Form */}
        <Grid item xs={12} md={8}>
          <Box
            sx={{
              display: 'flex',
              flexDirection: 'column',
              gap: 2,
            }}
          >
            <TextField
              fullWidth
              label="First Name"
              variant="outlined"
              sx={{
                backgroundColor: 'background.paper',
                borderRadius: 1,
              }}
            />
            <TextField
              fullWidth
              label="Last Name"
              variant="outlined"
              sx={{
                backgroundColor: 'background.paper',
                borderRadius: 1,
              }}
            />
            <TextField
              fullWidth
              label="Email"
              variant="outlined"
              sx={{
                backgroundColor: 'background.paper',
                borderRadius: 1,
              }}
            />
            <TextField
              fullWidth
              label="Phone"
              variant="outlined"
              sx={{
                backgroundColor: 'background.paper',
                borderRadius: 1,
              }}
            />
            <TextField
              fullWidth
              label="Company"
              variant="outlined"
              sx={{
                backgroundColor: 'background.paper',
                borderRadius: 1,
              }}
            />
            <TextField
              fullWidth
              label="Job Title"
              variant="outlined"
              sx={{
                backgroundColor: 'background.paper',
                borderRadius: 1,
              }}
            />
            <Button
              fullWidth
              variant="contained"
              sx={{
                backgroundColor: 'primary.main',
                color: 'secondary.main',
                '&:hover': {
                  backgroundColor: 'primary.dark',
                },
                fontWeight: 'bold',
                textTransform: 'none',
                mt: 2,
                padding: '12px 32px',
              }}
            >
              Contact Us
            </Button>
          </Box>
        </Grid>
      </Grid>
    </Box>
  );
};

export default ContactUs;
