// src/pages/Chiller.js

import React from 'react';
import {
  Box,
  Typography,
  Grid,
  Card,
  CardContent,
  Button,
  Accordion,
  AccordionSummary,
  AccordionDetails,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  Modal,
  Fade,
  Slide,
} from '@mui/material';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import CheckCircleIcon from '@mui/icons-material/CheckCircle';
import BodyImage from '../images/Body.jpg';
import ChatBubbleOutlineIcon from '@mui/icons-material/ChatBubbleOutline';
import VideoLibraryIcon from '@mui/icons-material/VideoLibrary';

const Chiller = () => {
  const keyFeatures = [
    'Real-Time Performance Monitoring',
    'Energy Efficiency Optimization',
    'Predictive Maintenance',
    'Fault Detection and Diagnostics',
    'Data Analytics and Reporting',
    'Automatic Setpoint Adjustments',
    'Integration with Building Management Systems (BMS)',
  ];

  const keyBenefits = [
    'Energy Cost Savings',
    'Extended Equipment Lifespan',
    'Reduced Operational Downtime',
    'Sustainability and Reduced Carbon Footprint',
    'Improved Comfort and Reliability',
  ];

  const userExperience = [
    'Intuitive Dashboard',
    'Customizable Alerts',
    'Mobile Accessibility',
  ];

  const securityCompliance = [
    'Data Security and Encryption',
    'Regulatory Compliance',
  ];

  const caseStudies = [
    {
      title: 'Commercial Building',
      description:
        'Enhancing efficiency with a 25% reduction in energy costs.',
      details:
        'In a commercial building setting, our Chiller Optimization Software successfully reduced energy consumption by 25%, leading to significant cost savings and improved operational efficiency.',
    },
    {
      title: 'Manufacturing Plant',
      description:
        'Optimizing operations and achieving significant cost savings.',
      details:
        'At a manufacturing plant, the implementation of our software resulted in optimized chiller performance, reducing operational costs and minimizing downtime.',
    },
  ];

  // State for Modal
  const [open, setOpen] = React.useState(false);
  const [currentCase, setCurrentCase] = React.useState(null);

  const handleOpen = (caseStudy) => {
    setCurrentCase(caseStudy);
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
    setCurrentCase(null);
  };

  return (
    <Box
      sx={{
        backgroundImage: `url(${BodyImage})`,
        backgroundAttachment: 'fixed',
        backgroundSize: 'cover',
        backgroundPosition: 'center',
        px: { xs: 2, md: 4 },
        py: 6,
        color: '#fff',
      }}
    >
      {/* Section: Title */}
      <Typography variant="h3" align="center" gutterBottom sx={{ fontWeight: 'bold' }}>
        Chiller Optimization Software
      </Typography>
      <Typography variant="h6" align="center" paragraph sx={{ opacity: 0.9 }}>
        Enhance performance, efficiency, and sustainability with cutting-edge technology.
      </Typography>

      {/* Section: Key Features */}
      <Box sx={{ mb: 6 }}>
        <Typography variant="h4" gutterBottom sx={{ fontWeight: 'bold' }}>
          Key Features
        </Typography>
        <Grid container spacing={4}>
          {keyFeatures.map((feature, index) => (
            <Grid item xs={12} sm={6} md={4} key={index}>
              <Accordion
                sx={{
                  backgroundColor: 'rgba(0, 0, 0, 0.6)',
                  borderRadius: 2,
                  color: '#fff',
                }}
              >
                <AccordionSummary
                  expandIcon={<ExpandMoreIcon sx={{ color: '#fff' }} />}
                  aria-controls={`panel-content-${index}`}
                  id={`panel-header-${index}`}
                >
                  <Typography variant="h6">{feature}</Typography>
                </AccordionSummary>
                <AccordionDetails>
                  <Typography variant="body2">
                    Detailed description about {feature}. This feature allows you to...
                  </Typography>
                </AccordionDetails>
              </Accordion>
            </Grid>
          ))}
        </Grid>
      </Box>

      {/* Section: Key Benefits */}
      <Box sx={{ mb: 6 }}>
        <Typography variant="h4" gutterBottom sx={{ fontWeight: 'bold' }}>
          Key Benefits
        </Typography>
        <Grid container spacing={4}>
          {keyBenefits.map((benefit, index) => (
            <Grid item xs={12} sm={6} md={4} key={index}>
              <Card
                sx={{
                  backgroundColor: 'rgba(0, 0, 0, 0.6)',
                  borderRadius: 2,
                  boxShadow: 3,
                  height: '100%',
                  display: 'flex',
                  alignItems: 'center',
                  padding: 2,
                  transition: 'transform 0.3s',
                  '&:hover': {
                    transform: 'translateY(-10px)',
                  },
                }}
              >
                <Box sx={{ display: 'flex', alignItems: 'center' }}>
                  <CheckCircleIcon sx={{ color: 'secondary.main', fontSize: '2rem', mr: 2 }} />
                  <Typography variant="body1">{benefit}</Typography>
                </Box>
              </Card>
            </Grid>
          ))}
        </Grid>
      </Box>

      {/* Section: User Experience and Interface */}
      <Box sx={{ mb: 6 }}>
        <Typography variant="h4" gutterBottom sx={{ fontWeight: 'bold' }}>
          User Experience and Interface
        </Typography>
        <Typography variant="body1" paragraph sx={{ opacity: 0.9 }}>
          Designed for both non-technical and technical users, our software ensures seamless interaction.
        </Typography>
        <Grid container spacing={4}>
          {/* Intuitive Dashboard */}
          <Grid item xs={12} sm={4}>
            <Card
              sx={{
                backgroundColor: 'rgba(0, 0, 0, 0.6)',
                borderRadius: 2,
                boxShadow: 3,
                height: '100%',
                padding: 2,
                transition: 'transform 0.3s',
                '&:hover': {
                  transform: 'scale(1.05)',
                },
              }}
            >
              <CardContent>
                <Typography variant="h6" sx={{ fontWeight: 'bold', mb: 1 }}>
                  Intuitive Dashboard
                </Typography>
                <Typography variant="body2">
                  Our dashboard provides real-time insights and easy navigation, making it simple to monitor and control chiller operations.
                </Typography>
              </CardContent>
            </Card>
          </Grid>
          {/* Customizable Alerts */}
          <Grid item xs={12} sm={4}>
            <Card
              sx={{
                backgroundColor: 'rgba(0, 0, 0, 0.6)',
                borderRadius: 2,
                boxShadow: 3,
                height: '100%',
                padding: 2,
                transition: 'transform 0.3s',
                '&:hover': {
                  transform: 'scale(1.05)',
                },
              }}
            >
              <CardContent>
                <Typography variant="h6" sx={{ fontWeight: 'bold', mb: 1 }}>
                  Customizable Alerts
                </Typography>
                <Typography variant="body2">
                  Set up personalized alerts to stay informed about system performance and receive notifications for any anomalies.
                </Typography>
              </CardContent>
            </Card>
          </Grid>
          {/* Mobile Accessibility */}
          <Grid item xs={12} sm={4}>
            <Card
              sx={{
                backgroundColor: 'rgba(0, 0, 0, 0.6)',
                borderRadius: 2,
                boxShadow: 3,
                height: '100%',
                padding: 2,
                transition: 'transform 0.3s',
                '&:hover': {
                  transform: 'scale(1.05)',
                },
              }}
            >
              <CardContent>
                <Typography variant="h6" sx={{ fontWeight: 'bold', mb: 1 }}>
                  Mobile Accessibility
                </Typography>
                <Typography variant="body2">
                  Access and manage your chiller systems on-the-go with our fully responsive mobile interface.
                </Typography>
              </CardContent>
            </Card>
          </Grid>
        </Grid>
      </Box>

      {/* Section: Security and Compliance */}
      <Box sx={{ mb: 6 }}>
        <Typography variant="h4" gutterBottom sx={{ fontWeight: 'bold' }}>
          Security and Compliance
        </Typography>
        <List>
          {securityCompliance.map((item, index) => (
            <ListItem key={index}>
              <ListItemIcon>
                <CheckCircleIcon sx={{ color: 'secondary.main' }} />
              </ListItemIcon>
              <ListItemText primary={item} />
            </ListItem>
          ))}
        </List>
      </Box>

      {/* Section: Case Studies */}
      <Box sx={{ mb: 6 }}>
        <Typography variant="h4" gutterBottom sx={{ fontWeight: 'bold' }}>
          Case Studies
        </Typography>
        <Grid container spacing={4}>
          {caseStudies.map((caseStudy, index) => (
            <Grid item xs={12} sm={6} key={index}>
              <Card
                sx={{
                  backgroundColor: 'rgba(0, 0, 0, 0.6)',
                  borderRadius: 2,
                  boxShadow: 3,
                  padding: 2,
                  transition: 'transform 0.3s',
                  '&:hover': {
                    transform: 'scale(1.02)',
                  },
                  cursor: 'pointer',
                }}
                onClick={() => handleOpen(caseStudy)}
              >
                <CardContent>
                  <Typography variant="h6" sx={{ fontWeight: 'bold', mb: 1 }}>
                    {caseStudy.title}
                  </Typography>
                  <Typography variant="body2">{caseStudy.description}</Typography>
                </CardContent>
              </Card>
            </Grid>
          ))}
        </Grid>
      </Box>

      {/* Modal for Case Study Details */}
      <Modal
        open={open}
        onClose={handleClose}
        closeAfterTransition
        aria-labelledby="case-study-title"
        aria-describedby="case-study-description"
      >
        <Fade in={open}>
          <Box
            sx={{
              position: 'absolute',
              top: '50%',
              left: '50%',
              transform: 'translate(-50%, -50%)',
              width: { xs: '90%', md: '60%' },
              bgcolor: 'background.paper',
              boxShadow: 24,
              p: 4,
              borderRadius: 2,
              outline: 'none',
            }}
          >
            {currentCase && (
              <>
                <Typography id="case-study-title" variant="h5" sx={{ mb: 2 }}>
                  {currentCase.title}
                </Typography>
                <Typography id="case-study-description" variant="body1" sx={{ mb: 4 }}>
                  {currentCase.details}
                </Typography>
                <Button variant="contained" onClick={handleClose}>
                  Close
                </Button>
              </>
            )}
          </Box>
        </Fade>
      </Modal>

      {/* Section: Integration with Other Systems */}
      <Box sx={{ mb: 6 }}>
        <Typography variant="h4" gutterBottom sx={{ fontWeight: 'bold' }}>
          Integration with Other Systems
        </Typography>
        <Grid container spacing={4}>
          {/* Seamless BMS Integration */}
          <Grid item xs={12} sm={6} md={4}>
            <Card
              sx={{
                backgroundColor: 'rgba(0, 0, 0, 0.6)',
                borderRadius: 2,
                boxShadow: 3,
                padding: 2,
                height: '100%',
                transition: 'transform 0.3s',
                '&:hover': {
                  transform: 'scale(1.05)',
                },
              }}
            >
              <CardContent>
                <Typography variant="h6" sx={{ fontWeight: 'bold', mb: 1 }}>
                  Seamless BMS Integration
                </Typography>
                <Typography variant="body2">
                  Our software effortlessly integrates with existing Building Management Systems, ensuring unified control and data synchronization.
                </Typography>
              </CardContent>
            </Card>
          </Grid>
          {/* Flexible API Options */}
          <Grid item xs={12} sm={6} md={4}>
            <Card
              sx={{
                backgroundColor: 'rgba(0, 0, 0, 0.6)',
                borderRadius: 2,
                boxShadow: 3,
                padding: 2,
                height: '100%',
                transition: 'transform 0.3s',
                '&:hover': {
                  transform: 'scale(1.05)',
                },
              }}
            >
              <CardContent>
                <Typography variant="h6" sx={{ fontWeight: 'bold', mb: 1 }}>
                  Flexible API Options
                </Typography>
                <Typography variant="body2">
                  Utilize our robust APIs to customize integrations and extend the capabilities of your existing systems.
                </Typography>
              </CardContent>
            </Card>
          </Grid>
        </Grid>
      </Box>

      {/* Section: Pricing and ROI */}
      <Box sx={{ mb: 6 }}>
        <Typography variant="h4" gutterBottom sx={{ fontWeight: 'bold' }}>
          Pricing and ROI
        </Typography>
        <Grid container spacing={4}>
          {/* Transparent Pricing */}
          <Grid item xs={12} sm={6} md={6}>
            <Card
              sx={{
                backgroundColor: 'rgba(0, 0, 0, 0.6)',
                borderRadius: 2,
                boxShadow: 3,
                padding: 2,
                height: '100%',
                display: 'flex',
                flexDirection: 'column',
                justifyContent: 'space-between',
              }}
            >
              <CardContent>
                <Typography variant="h6" sx={{ fontWeight: 'bold', mb: 1 }}>
                  Transparent Pricing
                </Typography>
                <Typography variant="body2">
                  Our pricing models are straightforward and flexible, ensuring you get the best value without hidden costs.
                </Typography>
              </CardContent>
            </Card>
          </Grid>
          {/* ROI Calculator */}
          <Grid item xs={12} sm={6} md={6}>
            <Card
              sx={{
                backgroundColor: 'rgba(0, 0, 0, 0.6)',
                borderRadius: 2,
                boxShadow: 3,
                padding: 2,
                height: '100%',
                display: 'flex',
                flexDirection: 'column',
                justifyContent: 'space-between',
              }}
            >
              <CardContent>
                <Typography variant="h6" sx={{ fontWeight: 'bold', mb: 1 }}>
                  ROI Calculator
                </Typography>
                <Typography variant="body2">
                  Estimate your return on investment with our easy-to-use ROI calculator, tailored to your specific needs and setup.
                </Typography>
                <Button
                  variant="outlined"
                  sx={{
                    mt: 2,
                    borderColor: 'secondary.main',
                    color: 'secondary.main',
                    '&:hover': {
                      backgroundColor: 'secondary.main',
                      color: 'primary.main',
                    },
                  }}
                >
                  Calculate ROI
                </Button>
              </CardContent>
            </Card>
          </Grid>
        </Grid>
      </Box>

      {/* Section: Demo and Support */}
      <Box sx={{ mb: 6 }}>
        <Typography variant="h4" gutterBottom sx={{ fontWeight: 'bold' }}>
          Demo and Support
        </Typography>
        <Grid container spacing={4}>
          {/* Free Trial or Demo */}
          <Grid item xs={12} sm={6} md={6}>
            <Card
              sx={{
                backgroundColor: 'rgba(0, 0, 0, 0.6)',
                borderRadius: 2,
                boxShadow: 3,
                padding: 2,
                height: '100%',
                display: 'flex',
                flexDirection: 'column',
                justifyContent: 'space-between',
              }}
            >
              <CardContent>
                <Typography variant="h6" sx={{ fontWeight: 'bold', mb: 1 }}>
                  Free Trial or Demo
                </Typography>
                <Typography variant="body2">
                  Experience the full capabilities of our Chiller Optimization Software with a free trial or personalized demo.
                </Typography>
              </CardContent>
              <Button
                variant="contained"
                sx={{
                  backgroundColor: 'secondary.main',
                  color: 'primary.main',
                  '&:hover': {
                    backgroundColor: 'grey.300',
                  },
                  fontWeight: 'bold',
                  textTransform: 'none',
                  mt: 2,
                }}
              >
                Request a Demo
              </Button>
            </Card>
          </Grid>
          {/* Ongoing Support */}
          <Grid item xs={12} sm={6} md={6}>
            <Card
              sx={{
                backgroundColor: 'rgba(0, 0, 0, 0.6)',
                borderRadius: 2,
                boxShadow: 3,
                padding: 2,
                height: '100%',
                display: 'flex',
                flexDirection: 'column',
                justifyContent: 'space-between',
              }}
            >
              <CardContent>
                <Typography variant="h6" sx={{ fontWeight: 'bold', mb: 1 }}>
                  Ongoing Support
                </Typography>
                <Typography variant="body2">
                  Our dedicated support team is here to assist you every step of the way, ensuring seamless operations and continuous improvement.
                </Typography>
              </CardContent>
              <Button
                variant="contained"
                sx={{
                  backgroundColor: 'secondary.main',
                  color: 'primary.main',
                  '&:hover': {
                    backgroundColor: 'grey.300',
                  },
                  fontWeight: 'bold',
                  textTransform: 'none',
                  mt: 2,
                }}
              >
                Contact Support
              </Button>
            </Card>
          </Grid>
        </Grid>
      </Box>
    </Box>
  );
};

export default Chiller;
